export const getUpcomingMovies = () =>
  fetch(
    'https://api.themoviedb.org/3/movie/upcoming?api_key=b3a72b722e11795c7179cc928980a9e2',
  )
    .then(res => res.json())
    .then(json => json.results);

export const getMovieDetails = (id: number) =>
  fetch(
    `https://api.themoviedb.org/3/movie/${id}?api_key=b3a72b722e11795c7179cc928980a9e2`,
  )
    .then(res => res.json())
    .then(json => json);
